<script>
import IgoosButton from "@/components/commons/igoosButton.vue";
import getData from "@/service/axios.js";
import { onMounted, ref } from 'vue';
export default {
    components: {
        IgoosButton,
    },
    setup() {
        const products = ref({});
        onMounted(async () => {
            const res = await getData();
            products.value = res.data;
            console.log(products.value);
        });
        return {
            products,
        }
    }
}
</script>

